    width: 100%;
    height: 34px;
    padding-left: 5px;
    text-align: left;
    font-size: 13px;
    border: 1px solid #ccc;
    border-radius: 4px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.modal input[type="checkbox"] {
  display: inline;
  margin-top: 15px;
  width: 15px;
}
.modal input[type="submit"] {
    color: #fff;
    background-color: #428bca;
    border-color: #357ebd;
    font-weight: bold;
    height: 36px;
    width: 100px;
    border: 1px;
    border-radius: 2px;
    margin: 11px 10px 11px 0px;
    text-align: center;
}
.modal input[type="button"] {
    background-color: #f2f2f2;
    color: #757575;
    font-weight: bold;
    height: 36px;
    width: 100px;
    border: 1px;
    border-radius: 2px;
